body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}
.header, .body, .footer {
  width: 414px;
  margin: 0 auto;
}
.header {
  height: 80px;
  background-color: #f1f1f1;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid #ccc;
}
.body {
  height: 370px;
  background-color: #fff;
  padding: 20px;
  box-sizing: border-box;
  border-bottom: 1px solid #ccc;
}
.footer {
  height: 1069px;
  background-color: #f1f1f1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.form-group {
  margin-bottom: 15px;
}
.input input {
  width: 100%;
  padding: 10px;
  margin-top: 5px;
  box-sizing: border-box;
}
.login-sifre-al-link-button a {
  display: inline-block;
  margin-top: 10px;
  color: #007bff;
  text-decoration: none;
}
.form-group button {
  width: 100%;
  padding: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  cursor: pointer;
}
.box-plate {
  border: 1px solid #ccc;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
}
.textbox-validator {
  font-family: Roboto;
  font-size: 12px;
  font-weight: 700;
  color: #a94442;
  padding-top: 6px;
  display: none;
}
